import React from "react";
import "./Feature.css";
import Featureitem from "./Featureitem";

function Feature() {
  return (
    <div className="feature">
      <h1 className="feature">ฟีเจอร์ TNC</h1>
      <div className="image-feature">
        <div className="div-img-feature-home1">
          <img className="img-feature-home1" src="images/home2.png"></img></div>
        <h3 className="h3-feature-home1">สะดวก</h3></div>
      <div className="image-feature">
        <div className="div-img-feature-home1">
          <img className="img-feature-home1" src="images/home1.png"></img></div>
        <h3 className="h3-feature-home1">โอนไว</h3></div>
      <div className="image-feature">
        <div className="div-img-feature-home1">
          <img className="img-feature-home1" src="images/home3.png"></img></div>
        <h3 className="h3-feature-home1">รวดเร็ว</h3></div>
      <div className="image-feature">
        <div className="div-img-feature-home1">
          <img className="img-feature-home1" src="images/home4.png"></img></div>
        <h3 className="h3-feature-home1">ปลอดภัย</h3></div>
      <h1 className="feature">คุณสมบัติ TNC</h1>
      <div className="feature__container">
        <div className="feature__wrapper">
          <ul className="feature__items">
            <Featureitem
              src="images/img-9.jpg"
              text="ร้านค้าธุรกิจต่างๆ สามารถโอนแต้มสะสมกับลูกค้า เมื่อลูกค้าซื้อสินค้าตามข้อกำหนดที่ตั้งไว้ ลูกค้าเองก็สามารถโอนแต้มสะสมกับคนอื่นๆ ได้เช่นกัน"
              label="สมัครเลย"
              path="/registration"
            />
            <Featureitem
              src="images/img-2.jpg"
              text="ลูกค้าสามารถตรวจสอบแต้มสะสม เพื่อใช้ในการรับของสมานาคุณต่างๆ ผ่านเว็บTNC ได้อย่างง่ายๆ เพียงกรอกเบอร์โทร เพื่อแลกของรางวัลต่างๆ"
              label="สมัครเลย"
              path="/registration"
            />
            <Featureitem
              src="images/img-4.jpg"
              text="ลูกค้าและธุรกิจร้านค้าต่างๆ สามารถแก้ไขข้อมูลส่วนตัวได้ หมดห่วงเรื่องความปลอดภัยในการโอนแต้มเพราะใช้เทคโนโลยีบล็อกเชนเข้ามาช่วย"
              label="สมัครเลย"
              path="/registration"
            />

            <Featureitem
              src="images/img-3.jpg"
              text="ลูกค้าและธุรกิจร้านค้า สามารถตรวจสอบประวัติการโอนแต้มสะสมย้อนหลัง,ตรวจสอบประวัติการซื้อสินค้าได้อีกด้วย และคุณสมบัติอื่นๆอีกมากมายใน TNC "
              label="สมัครเลย"
              path="/registration"
            />
          </ul>
          {/* <ul className="feature__items">
            

            <Featureitem
              src="images/img-8.jpg"
              text="สะดวกสบาย รวดเร็ว ปลอดภัย"
              label="สมัครเลย"
              path="/registration"
           />
          </ul>*/}
        </div>
      </div>
    </div>
  );
}

export default Feature;
