import React from "react";

import Herosection from "./Herosection";
import Feature from "./Feature";
import { motion } from "framer-motion";
import "./Home.css";

export default function Home() {
  /* ********************************** */

  const pageTransition = {
    in: {
      opacity: 1,
      y: 0,
    },
    out: {
      opacity: 0,
      y: 0,
    },
  };

  /* ********************************** */

  return (
    <div>
      <nav className="navbar navbar-light bg-light justify-content-between">
        <a className="nav-tnc">
          <i className="fas fa-braille"></i>T N C
        </a>
        <div className="div-img-logo">
          <img className="img-logoo" src="/images/logo.png" />
        </div>
        <form action="/login" className="form-inline">
          <button className="bn632-hover bn24">เข้าสู่ระบบ</button>
        </form>
      </nav>

      <motion.div
        initial="out"
        animate="in"
        exit="out"
        variants={pageTransition}
      >
        <div>
          <Herosection />
        </div>
        <div>
          <Feature />
        </div>
      </motion.div>
    </div>
  );
}
